import { Form, Modal, Input, DatePicker, message } from "antd";
import moment from "moment";
import React from "react";
import { addRecord } from "../service";
import {dateFormat} from "../../../utils/dateFormat"

export default class AddRecord extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      confirmLoading: false,
    };
  }
  addRecordRef = React.createRef();
  addRecordComfirm = () => {
    this.addRecordRef.current.validateFields()
    .then((values) => {
      const params = {...values, date: dateFormat(values.date)}
      addRecord(params).then(res => {
        // console.log(res);
        if(res.data.status === 200) {
          message.success("")
        } else if (res.data.status === 100){
          message.error("")
        }
        this.props.gatherRecordList()
      })
      this.props.setAddVisibleFn(false);
    })
    .catch((err) => {})
  };
  cancelAdd = () => {
    // 

    this.props.setAddVisibleFn(false);
  };
  render() {
    const { visible } = this.props;
    const formLayout = {
      labelCol: { span: 4 },
      wrapperCol: { span: 20 },
    };
    return (
      <div className="addrecord-container">
        <Modal
          open={visible}
          title=""
          onOk={this.addRecordComfirm}
          onCancel={this.cancelAdd}
          okText=""
          cancelText=""
          confirmLoading={this.state.confirmLoading}
        >
          <Form
            ref={this.addRecordRef}
            name="addForm"
            // preserve={false}
            {...formLayout}
          >
            <Form.Item
              name="company"
              label=""
              rules={[
                {
                  required: true,
                  message: "",
                  trigger: "blur",
                },
              ]}
            >
              <Input placeholder="" />
            </Form.Item>
            <Form.Item
              name="link"
              label=""
            >
              <Input placeholder="" />
            </Form.Item>
            <Form.Item name="office" label="">
              <Input placeholder="" />
            </Form.Item>
            <Form.Item name="station" label="">
              <Input placeholder="" />
            </Form.Item>
            <Form.Item name="city" label="">
              <Input placeholder="" />
            </Form.Item>
            <Form.Item
              name="date"
              label=""
              rules={[{ required: true, message: "" }]}
            >
              <DatePicker
                disabledDate={(curdate) =>
                  curdate.diff(moment().format("YYYY-MM-DD"), "days") > 0
                }
              ></DatePicker>
            </Form.Item>
          </Form>
        </Modal>
      </div>
    );
  }
}
