import React from "react";
import { Form, Input, Button, Checkbox, message } from "antd";
import { UserOutlined, LockOutlined } from "@ant-design/icons";
import { userLogin } from "../../utils/service";
import "./index.css";
class Login extends React.Component {
  loginInfoForm = React.createRef();
  loginCheck = async () => {
      this.loginInfoForm.current.validateFields()
      .then(async data => 
        {
          const res = await userLogin(data)
        if (res.data.status == 200){
          message.success('')
          sessionStorage.setItem("token", res.data.token)
          window.location.href = '/list'
          console.log(this.props);
        } else {
          message.error(res.data.msg)
        }
        }
      )
  }

  render() {
    const formItemLayout = {
      labelCol: {
        span: 4,
      },
      wrapperCol: {
        span: 20,
      },
      
    };
    const userRules = [
      { required: true, message: "", trigger: "blur" },
      {
        max: 16,
        min: 3,
        message: "3-16",
        validateTrigger: "onBlur",
      },
    ];
    const passwordRules = [{ required: true, message: "" }];

    return (
      <div className="login-container">
        <div className="login-content">
          <div className="login-wrapper">
            <Form ref={this.loginInfoForm} name="login">
              <Form.Item
                {...formItemLayout}
                name="username"
                label=""
                rules={userRules}
                validateTrigger="onBlur"
              >
                <Input placeholder="" prefix={<UserOutlined />} />
              </Form.Item>
              <Form.Item
                {...formItemLayout}
                name="pwd"
                label=""
                rules={passwordRules}
                validateTrigger="onBlur"
              >
                <Input.Password
                  placeholder=""
                  prefix={<LockOutlined />}
                />
              </Form.Item>
              <Form.Item>
                <Form.Item name="remember" valuePropName="checked" 
                noStyle
                >
                  <Checkbox style={{float: 'left'}}></Checkbox>
                </Form.Item>

                <a className="fr" href="">
                  
                </a>
              </Form.Item>

              <Form.Item
              style={{marginBottom: '0px'}}
              >
                <Button
                  type="primary"
                  htmlType="submit"
                  className="login-form-button"
                  onClick={this.loginCheck}
                >
                  
                </Button>
                 <a href="/register"></a>
              </Form.Item>
            </Form>
          </div>
        </div>
      </div>
    );
  }
}

export default Login;
